









































import Vue from 'vue';
import spec from './testdata.json';
import Hub from '@/components/Hub.vue';
import Definition from '@/components/Definition.vue';
import { Spec, specBuilder } from './models/Spec';

declare global {
  interface Window {
    baseURL: string;
  }
}

export default Vue.extend({
  name: 'App',
  components: {
    Hub,
    Definition,
  },
  data() {
    return {
      specDef: null as Record<string, any> | null,
      baseURL: null as string | null,
      loading: false,
    };
  },
  computed: {
    spec(): Spec {
      if (this.specDef) {
        return specBuilder(this.specDef);
      }
      return { hubs: [], definitions: [] };
    },
    specUrl(): string {
      return this.baseURL + 'sigspec/v1/sigspec.json';
    },
  },
  created() {
    if (window.baseURL) {
      this.baseURL = window.baseURL;
      this.loadSpec();
    } else {
      this.specDef = spec;
    }

    return true;
  },
  methods: {
    loadSpec() {
      this.loading = true;
      this.specDef = null;
      fetch(this.specUrl, { mode: 'cors' })
        .then((response) => {
          return response.text();
        })
        .then((text) => {
          this.specDef = JSON.parse(text);
        })
        .catch((error) => {
          alert('Request failed' + error);
        })
        .finally(() => {
          this.loading = false;
        });
    },
    definition(path: string) {
      const parts = path.split('/');
      const name = parts[parts.length - 1];
      return (this.spec.definitions as any)[name];
    },
    propertyType(prop: string[]) {
      const type = prop[prop.length - 1];
      const nullable = prop[0] == 'null';

      return `${type}${nullable ? '!' : ''}`;
    },
  },
});
