









































import Vue from 'vue';

export default Vue.extend({
    props: {
        definition: Object
    },
    data() {
        return {
            open: false
        };
    },
    methods: {
        propertyType(prop: string[]) {
            const type = prop[prop.length - 1];
            const nullable = prop[0] == 'null';

            return `${type}${nullable ? '?' : ''}`;
        }
    }
});
